/* Course Detail Page Styles */
.course-detail-page {
  min-height: 100vh;
  background: #000000;
  color: #ffffff;
}

/* Hero Section */
.course-detail-hero {
  background: #0f0f0f;
  color: white;
  padding: 8rem 0 7rem;
  position: relative;
  overflow: hidden;
  border-bottom: 1px solid rgba(255, 255, 255, 0.05);
}

.course-detail-hero::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: radial-gradient(circle at 20% 80%, rgba(87, 188, 235, 0.05) 0%, transparent 50%),
              radial-gradient(circle at 80% 20%, rgba(87, 188, 235, 0.05) 0%, transparent 50%);
  pointer-events: none;
}

.course-detail-hero-content {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 2rem;
  position: relative;
  z-index: 1;
}

.course-detail-back-link {
  display: inline-flex;
  align-items: center;
  background: rgba(87, 188, 235, 0.15);
  color: #57bceb;
  padding: 0.75rem 1.5rem;
  border-radius: 0.5rem;
  text-decoration: none;
  border: 1px solid rgba(87, 188, 235, 0.3);
  cursor: pointer;
  font-size: 0.875rem;
  margin-bottom: 2rem;
  margin-top: 0;
  backdrop-filter: blur(10px);
  transition: all 0.3s ease;
}

.course-detail-back-link:hover {
  background: rgba(87, 188, 235, 0.25);
  color: #ffffff;
  transform: translateX(-4px);
}

.course-detail-hero-main {
  display: grid;
  grid-template-columns: 1fr 400px;
  gap: 3rem;
  align-items: center;
}

.course-detail-meta {
  display: flex;
  align-items: center;
  gap: 1rem;
  margin-bottom: 1rem;
}

.course-detail-product {
  font-size: 1.25rem;
  font-weight: 600;
  color: rgba(255, 255, 255, 0.7);
}

.course-detail-level-badge {
  padding: 0.4rem 0.8rem;
  border-radius: 16px;
  font-size: 0.75rem;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  border: 1px solid;
  backdrop-filter: blur(10px);
}

.course-detail-level-basic {
  background: rgba(209, 250, 229, 0.98);
  color: #047857;
  border-color: rgba(167, 243, 208, 0.9);
}

.course-detail-level-intermediate {
  background: rgba(255, 243, 224, 0.98);
  color: #e65100;
  border-color: rgba(255, 183, 77, 0.9);
}

.course-detail-level-advanced {
  background: rgba(219, 234, 254, 0.98);
  color: #1e40af;
  border-color: rgba(147, 197, 253, 0.9);
}

.course-detail-level-individual {
  background: rgba(224, 247, 250, 0.98);
  color: #006064;
  border-color: rgba(178, 235, 242, 0.9);
}

.course-detail-title {
  font-size: 3rem;
  font-weight: 700;
  line-height: 1.2;
  margin-bottom: 1rem;
  color: #ffffff;
  letter-spacing: -0.02em;
}

.course-detail-code {
  font-size: 1.125rem;
  color: rgba(255, 255, 255, 0.6);
  margin-bottom: 1.5rem;
}

.course-detail-description {
  font-size: 1.125rem;
  line-height: 1.8;
  color: rgba(255, 255, 255, 0.7);
}

.course-detail-hero-image {
  position: relative;
}

.course-detail-image {
  width: 100%;
  height: 300px;
  object-fit: cover;
  border-radius: 1rem;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.6);
  border: 1px solid rgba(255, 255, 255, 0.1);
}

/* Main Content */
.course-detail-content {
  padding: 5rem 0;
  background: #000000;
}

.course-detail-container {
  max-width: 1400px;
  margin: 0 auto;
  padding: 0 2rem;
}

.course-detail-grid {
  display: grid;
  grid-template-columns: 2.2fr 1fr;
  gap: 4rem;
  align-items: start;
}

.course-detail-main {
  background: rgba(0, 0, 0, 0.4);
  padding: 3rem;
  border-radius: 1.25rem;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.4);
  border: 1px solid rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(8px);
}

.course-detail-section {
  margin-bottom: 3rem;
  padding-bottom: 3rem;
  padding-top: 2rem;
  border-bottom: 1px solid rgba(255, 255, 255, 0.05);
}

.course-detail-section:last-child {
  margin-bottom: 0;
  padding-bottom: 0;
  border-bottom: none;
}

.course-detail-section h2 {
  font-size: 1.75rem;
  font-weight: 700;
  color: #ffffff;
  margin-bottom: 2rem;
  margin-top: 3rem;
  letter-spacing: -0.02em;
}

.course-detail-section:first-child h2 {
  margin-top: 0;
}

/* Adjust spacing for larger screens */
@media (min-width: 1024px) {
  .course-detail-section {
    margin-bottom: 3.5rem;
    padding-bottom: 3.5rem;
  }

  .course-detail-section:last-child {
    margin-bottom: 0;
    padding-bottom: 0;
  }
}

.course-detail-rich-content {
  line-height: 1.75;
  color: rgba(255, 255, 255, 0.8);
  font-size: 1.05rem;
  letter-spacing: 0.01em;
}

.course-detail-rich-content p {
  margin-bottom: 1.25rem;
}

.course-detail-rich-content p:last-child {
  margin-bottom: 0;
}

/* Add bottom margin to rich content on larger screens for better section spacing */
@media (min-width: 1024px) {
  .course-detail-rich-content {
    margin-bottom: 1.5rem;
  }
}

.course-detail-rich-content strong {
  color: #ffffff;
  font-weight: 700;
}

.course-detail-rich-content em {
  color: #57bceb;
  font-style: italic;
}

.course-detail-rich-content .tech-term {
  color: #57bceb;
  font-weight: 600;
}

.course-detail-rich-content .key-info-box {
  background: rgba(87, 188, 235, 0.1);
  border: 1px solid rgba(87, 188, 235, 0.3);
  border-radius: 0.75rem;
  padding: 1.5rem;
  margin: 1.5rem 0;
}

.course-detail-rich-content .key-info-box .title {
  font-weight: 700;
  color: #57bceb;
  margin-bottom: 0.5rem;
}

.course-detail-rich-content .success-box {
  background: rgba(16, 185, 129, 0.1);
  border: 1px solid rgba(16, 185, 129, 0.3);
  border-radius: 0.75rem;
  padding: 1.5rem;
  margin: 1.5rem 0;
}

.course-detail-rich-content .warning-box {
  background: rgba(245, 158, 11, 0.1);
  border: 1px solid rgba(245, 158, 11, 0.3);
  border-radius: 0.75rem;
  padding: 1.5rem;
  margin: 1.5rem 0;
}

.course-detail-contents {
  display: flex;
  flex-direction: column;
  gap: 1.25rem;
}

.course-detail-content-item {
  padding: 1.5rem;
  background: rgba(255, 255, 255, 0.03);
  border-radius: 1rem;
  border: 1px solid rgba(255, 255, 255, 0.1);
  margin-bottom: 1rem;
}

.course-detail-content-item:last-child {
  margin-bottom: 0;
}

.course-detail-advantages {
  margin: 0;
  padding: 0;
  width: 100%;
}

.course-detail-advantage-item {
  display: block;
  margin-bottom: 1rem;
  line-height: 1.6;
  width: 100%;
  color: rgba(255, 255, 255, 0.8);
  padding-left: 2rem;
  position: relative;
}

.course-detail-advantage-item:last-child {
  margin-bottom: 0;
}

.course-detail-advantage-item::before {
  content: 'âœ“';
  color: #10b981;
  font-weight: 700;
  font-size: 1rem;
  position: absolute;
  left: 0.5rem;
  top: 0;
}

/* Sidebar */
.course-detail-sidebar {
  display: flex;
  flex-direction: column;
  gap: 2rem;
  position: sticky;
  top: 2rem;
  height: fit-content;
}

.course-detail-info-card,
.course-detail-cta-card {
  background: rgba(0, 0, 0, 0.4);
  padding: 2.5rem;
  border-radius: 1.25rem;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.4);
  border: 1px solid rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(8px);
}

.course-detail-info-card h3,
.course-detail-cta-card h3 {
  font-size: 1.375rem;
  font-weight: 700;
  color: #ffffff;
  margin-bottom: 2rem;
  margin-top: 0;
  letter-spacing: -0.02em;
}

.course-detail-info-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem 0;
  border-bottom: 1px solid rgba(255, 255, 255, 0.05);
  transition: all 0.2s ease;
}

.course-detail-info-item:hover {
  padding-left: 0.5rem;
  background-color: rgba(255, 255, 255, 0.05);
  margin: 0 -1rem;
  padding-left: 1rem;
  padding-right: 1rem;
  border-radius: 0.5rem;
  border-bottom: 1px solid transparent;
}

.course-detail-info-item:last-child {
  border-bottom: none;
}

.course-detail-info-label {
  font-weight: 600;
  color: rgba(255, 255, 255, 0.6);
  font-size: 0.95rem;
}

.course-detail-info-value {
  color: #ffffff;
  font-weight: 600;
  font-size: 0.95rem;
}

.course-detail-pricing {
  color: rgba(255, 255, 255, 0.8);
  line-height: 1.6;
}

.course-detail-cta-card p {
  color: rgba(255, 255, 255, 0.7);
  margin-bottom: 1.5rem;
  line-height: 1.6;
}

.course-detail-cta-btn {
  width: 100%;
  background: transparent;
  color: #57bceb;
  border: 2px solid rgba(255, 255, 255, 0.2);
  padding: 1rem 2rem;
  border-radius: 0.6rem;
  font-weight: 700;
  font-size: 1rem;
  cursor: pointer;
  transition: all 0.3s ease;
}

.course-detail-cta-btn:hover {
  background: rgba(255, 255, 255, 0.1);
  color: #ffffff;
  border-color: rgba(255, 255, 255, 0.3);
}

/* Not Found */
.course-detail-not-found {
  min-height: 50vh;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  padding: 2rem;
}

.course-detail-not-found h1 {
  font-size: 2rem;
  color: #ffffff;
  margin-bottom: 1rem;
}

.course-detail-not-found p {
  color: rgba(255, 255, 255, 0.7);
  margin-bottom: 2rem;
}

.course-detail-back-btn {
  background: rgba(255, 255, 255, 0.1);
  color: #ffffff;
  border: 2px solid rgba(255, 255, 255, 0.2);
  padding: 1rem 2rem;
  border-radius: 0.75rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
}

.course-detail-back-btn:hover {
  transform: translateY(-2px);
  background: rgba(255, 255, 255, 0.2);
  border-color: rgba(255, 255, 255, 0.3);
}

/* Responsive Design */
@media (max-width: 1024px) {
  .course-detail-hero-main {
    grid-template-columns: 1fr;
    gap: 2rem;
  }

  .course-detail-grid {
    grid-template-columns: 1fr;
    gap: 3rem;
  }
}

/* Specific breakpoint for small screens between tablet and phone */
@media (max-width: 600px) {
  .course-detail-page {
    width: 100vw;
    max-width: 100vw;
    margin: 0;
    padding: 0;
    overflow-x: hidden;
  }

  .course-detail-hero {
    padding: 6rem 0 5rem;
    width: 100vw;
    max-width: 100vw;
    margin: 0;
  }

  .course-detail-hero-content {
    padding: 0 1rem;
    max-width: 100%;
    width: 100%;
  }

  .course-detail-container {
    padding: 0 1rem;
    max-width: 100%;
    width: 100%;
  }

  .course-detail-main {
    padding: 2rem 1.5rem;
    width: 100%;
    box-sizing: border-box;
  }

  .course-detail-content {
    padding: 2rem 0;
  }

  .course-detail-sidebar {
    width: 100%;
  }

  .course-detail-info-card,
  .course-detail-cta-card {
    padding: 1.5rem;
    width: 100%;
    box-sizing: border-box;
  }
}

@media (max-width: 768px) {
  .course-detail-hero {
    padding: 6rem 0 5rem;
  }

  .course-detail-hero-content {
    padding: 0 1rem;
  }

  .course-detail-hero-main {
    gap: 1.5rem;
  }

  .course-detail-title {
    font-size: 1.75rem;
    line-height: 1.2;
  }

  .course-detail-description {
    font-size: 1rem;
    line-height: 1.6;
  }

  .course-detail-image {
    height: 200px;
  }

  .course-detail-meta {
    flex-wrap: wrap;
    gap: 0.75rem;
  }

  .course-detail-product {
    font-size: 1rem;
  }

  .course-detail-level-badge {
    padding: 0.3rem 0.6rem;
    font-size: 0.7rem;
  }

  .course-detail-container {
    padding: 0 1rem;
  }

  .course-detail-section,
  .course-detail-info-card,
  .course-detail-cta-card {
    padding: 1.5rem;
  }

  .course-detail-content {
    padding: 2rem 0;
  }

  .course-detail-main {
    gap: 2rem;
  }
}

/* Large screens */
@media (min-width: 1200px) {
  .course-detail-title {
    font-size: 3.5rem;
  }
}

/* Animation Classes */
.animate-on-scroll {
  opacity: 0;
  transform: translateY(30px);
  transition: all 0.6s ease;
}

.animate-on-scroll.slide-left {
  transform: translateX(30px);
}

.animate-on-scroll.visible {
  opacity: 1;
  transform: translate(0, 0);
}
